






















































































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import CStatistics from '@/components/links/CStatistics.vue';
import CButton from '@/components/CButton.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';
import MathUtil from '@/typescript/MathUtil.ts'
import StatisticsCalc from '@/typescript/StatisticsCalc.ts'

@Component({
  components: {
    CStatistics,
    CButton,
    CSelectorSample,
  },
  head: {
    title: {
      inner: '正規乱数',
    },
  },
})
export default class NormalDistribution extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataButtonText: string = '出力';
  private dataKeyRegExps: {
    sample: RegExp,
  } = {
    sample: /\{ *sample\ *\}/g,
  };
  private dataFormat: string = '';
  private dataSelectedFormatExampleIndex: number = 0;
  private dataFormatExamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'comma',
      caption: 'カンマ区切り',
      content: '{ sample },',
    },
    {
      name: 'new_line',
      caption: '改行区切り',
      content: '{ sample }\n',
    },
    {
      name: 'semicolon',
      caption: 'セミコロン区切り',
      content: '{ sample }:',
    },
    {
      name: 'slash',
      caption: 'スペース区切り',
      content: '{ sample } ',
    },
  ];
  private dataSelectedSampleExampleIndex: number = 0;
  private dataSampleExamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: ',,,',
    },
    {
      name: 'standard_normal_distribution',
      caption: '標準正規分布',
      content: '1000,0,1,4',
    },
    {
      name: 'height_male',
      caption: '20-24歳男子身長',
      content: '1000,171.56,5.59,2',
    },
    {
      name: 'height_female',
      caption: '20-24歳女子身長',
      content: '1000,158.28,5.29,2',
    },
    {
      name: 'test',
      caption: 'センター試験（国語）',
      content: '1000,111.29,33.10,0',
    },
  ];
  private dataSampleSize: string = '';
  private dataMean: string = '';
  private dataStandardDiviation: string = '';
  private dataDecimalPlace: string = '';
  private dataText: string = '';

  private created(): void {
  }

  // Methods
  private listenSampleSizeInput($event: Event): void {
    let number: string = (<HTMLInputElement>(<any>$event).target).value;
    if (MathUtil.isNaturalNumberIncludingZero(number)) {
      this.dataSampleSize = number;
    } else {
      this.dataSampleSize = '';
    }
  }
  private listenMeanInput($event: Event): void {
    let number: string = (<HTMLInputElement>(<any>$event).target).value;
    if (MathUtil.isRealNumber(number)) {
      this.dataMean = number;
    } else {
      this.dataMean = '';
    }
  }
  private listenStandardDiviationInput($event: Event): void {
    let number: string = (<HTMLInputElement>(<any>$event).target).value;
    if (MathUtil.isRealNumber(number)) {
      this.dataStandardDiviation = number;
    } else {
      this.dataStandardDiviation = '';
    }
  }
  private listenDecimalPlaceInput($event: Event): void {
    let number: string = (<HTMLInputElement>(<any>$event).target).value;
    if (MathUtil.isNaturalNumberIncludingZero(number)) {
      this.dataDecimalPlace = number;
    } else {
      this.dataDecimalPlace = '';
    }
  }
  private listenInput($event: Event): void {
    this.dataFormat = (<HTMLInputElement>(<any>$event).target).value;
  }
  private listenOnFormatExampleSelect($event: Event): void {
    let index: number = Number(<any>$event);
    this.dataFormat = this.dataFormatExamples[index].content;
    this.dataSelectedFormatExampleIndex = index;
  }
  private listenOnSampleExampleSelect($event: Event): void {
    let index: number = Number(<any>$event);
    let samples: string[] = this.dataSampleExamples[index].content.split(',');
    this.dataSampleSize = samples[0];
    this.dataMean = samples[1];
    this.dataStandardDiviation = samples[2];
    this.dataDecimalPlace = samples[3];
    this.dataSelectedSampleExampleIndex = index;
  }
  private outputText(): void {
    this.dataText = '';
    if (this.dataFormat !== ''
      && this.dataSampleSize !== '' 
      && this.dataMean !== '' 
      && this.dataStandardDiviation !== '') {
      let normalRandoms: number[] = [];
      for (let i: number = 0; i < Number(this.dataSampleSize); i++) {
        normalRandoms.push(
          StatisticsCalc.generateNormalRandom(
            Number(this.dataMean),
            Number(this.dataStandardDiviation),
          )
        );
      }
      this.formatSamples(normalRandoms);
    }
  }
  private formatSamples(normalRandoms: number[]): void {
    let text: string = '';
    for (let i: number = 0; i < normalRandoms.length; i++) {
      let normalRandomText: string = this.dataFormat;
      normalRandomText = normalRandomText.replace(
        this.dataKeyRegExps.sample,
        MathUtil.toFixed(normalRandoms[i], Number(this.dataDecimalPlace))
      );
      text += normalRandomText;
    }
    this.dataText = text;
  }
}
